/* Base */

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  transition: all 0.2s ease-in-out;
  background-repeat: no-repeat;
  /* projects that use icons. It doesn't affect much on most layouts I tried applying it on, as it only really comes up when inline or inline-block elements line up with each other on the same line. */
  vertical-align: middle;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
  -webkit-font-smoothing: antialiased;
}

html {
  /* defines 1 rem = 62.5% of browser default rem which is 16px
  this leads to 1 rem = 62.5% of 16px = 10px to could make every font and size with rem and this is for easy responsive */
  font-size: 62.5%;
  @include respond(big-desktop) {
    font-size: 75%; //want: 1rem = 12px, 12/16px(base) = 75%
  }
  @include respond(tab-land) {
    font-size: 56.25%; //want: 1rem = 9px, 9/16px(base) = 56.25%
  }
  @include respond(tab-port) {
    font-size: 50%; //want: 1rem = 8px, 8/16px(base) = 50%
  }
}

body {
  box-sizing: border-box;
  background-color: #f9f9f9;
}

::selection {
  color: $color-white;
  background: $Light-green;
}

::placeholder {
  color: $main-gray;
}

a {
  cursor: pointer;
  text-decoration: none !important;
  color: $main-gray;
  &:hover {
    color: $nav-main-color;
  }
}

img {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.dropdown-menu {
  @include dropDownShadow;
}

.dropdown-item {
  font-size: 1.3rem;
  padding: .25rem 1.5rem;
  &>.fa {
    padding-right: 6px;
  }
  &:hover {
    color: #fff !important;
    background-color: $nav-main-color;
  }
}

/* Base end-------------------- */